body {
  margin: 0;
  padding: 0;
  overflow-y: auto;
  overflow-x: hidden;
}

.App {
    color: white;
    margin-top: 7%;
    font-family: "open sans";
}

.content {
    margin: 2.3% 6.7% 0% 6.7%;
}

.head {
    width: 50%;
    font-size: 18px;
    margin: 6.7%;
}

.footer {
    padding-top: 70vh;
}

.cl {
    text-align: center;
}

.clock {
    display: inline-block;
    background: white !important;
    background-color: white !important;
}

#goGreen {
    color: #009933;
    font-size: 24px;
}

.about {
    margin: 35% 6.7% 6.7% 6.7%;
    font-size: 2.5vw;
    margin-top: 65vh;
}

.img {
    -webkit-animation: float 5s infinite;
            animation: float 5s infinite;
    float: left;
    animation-direction: alternate-reverse;
}

@-webkit-keyframes float {
    0% {
        -webkit-transform: translate(0%, 0%);
                transform: translate(0%, 0%);
    }

    100% {
        -webkit-transform: translate(-7%, 7%);
                transform: translate(-7%, 7%);
    }
}

@keyframes float {
    0% {
        -webkit-transform: translate(0%, 0%);
                transform: translate(0%, 0%);
    }

    100% {
        -webkit-transform: translate(-7%, 7%);
                transform: translate(-7%, 7%);
    }
}

@media only screen and (min-width: 1000px) {
    .about {
        font-size: 23px;
    }
}

@media only screen and (max-width: 600px) {
    .about {
        font-size: 18px;
    }
}

.imge:hover {
    -webkit-animation: zoo .4s;
            animation: zoo .4s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.imge {
    cursor: pointer;
}

@-webkit-keyframes zoo {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    100% {
        -webkit-transform: scale(1.06);
                transform: scale(1.06);
    }
}

@keyframes zoo {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    100% {
        -webkit-transform: scale(1.06);
                transform: scale(1.06);
    }
}

pre {
    background-color: white;
    color: #232351;
    position: relative;
    width: 500px;
    z-index: 99;
    overflow-y: scroll;
    overflow-x: auto;
    display: inline-block;
    font-size: 10px;
    height: 500px;
    float: left;
    -webkit-animation: movt 1s;
            animation: movt 1s;
  text-align: left;
}
@-webkit-keyframes movt{
  0%{
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@keyframes movt{
  0%{
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
