.cl2 {
    position: relative;
    -webkit-animation: cl 12s infinite;
            animation: cl 12s infinite;
    opacity: .7;
    top: 30px;
}

.cl3 {
    position: relative;
    -webkit-animation: cl 10s infinite;
            animation: cl 10s infinite;
    opacity: .7;
    top: 80px;
}

.cl4 {
    position: relative;
    -webkit-animation: cl 8s infinite;
            animation: cl 8s infinite;
    opacity: .7;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
    left: 50px;
    top: 90px;
}

.cl5 {
    position: relative;
    -webkit-animation: cl 12s infinite;
            animation: cl 12s infinite;
    opacity: .7;
    top: 20px;
}

.cl6 {
    position: relative;
    -webkit-animation: cl 12s infinite;
            animation: cl 12s infinite;
    opacity: .5;
    left: 60px;
    top: 110px;
}

.cl7 {
    position: relative;
    -webkit-animation: cl 12s infinite, opa 5s;
